html, body {
  font-family: 'Nunito Sans', Arial, Helvetica, sans-serif;
}

.app {
  display: flex;
  width: 100%;
}

.app .content, .app .api-result {
  margin: 20px;
}

.app .content {
  background: #101112;
  color:  white;
  min-height: 90vh;
  width:  70%;
  padding: 20px;
}

.app .api-result {
  width: 30%;
  word-break: break-word;
  color: grey;
}

.product-container {
  display: flex;
  flex-wrap: wrap;
}

.product {
  width: 50%;
  margin-bottom: 20px;
}

.product-details {
  display: flex;
  justify-content: space-between;
}

.app .grouping {
  width: 25%;
  margin-bottom: 20px;
  min-height: 100px;
}

.app .grouping-container {
  display: flex;
  flex-wrap: wrap;
}

.app .grouping-background {
  background: rgba(255, 255, 255, 0.3);
  height: 100%;
  margin: 10px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}

.app .grouping-background:hover {
  background:  rgba(255, 255, 255, 0.6);
  transition:  background 0.5s ease-out;
}

.app .grouping-background.selected{
  background: rgba(40, 235, 207, 0.3);
  transition:  background 0.5s ease-out;
}

.app .grouping-padding {
  padding: 20px;
  width: 100%;
}

.app .grouping-padding * {
  text-align: center;
}

.article-teaser {
  margin-bottom: 60px;
}

.article-teaser p {
  margin-bottom: 8px;
  margin-top: 8px;
}

.article-green {
  color: #28EBCF;
}
.article-green:hover {
  text-decoration: underline;
  cursor: pointer;
}

.button-container {
  display: flex;
  justify-content: space-between;
}

img.responsive {
  width: 70%;
  height: auto;
  display: flex;
  margin: auto;
}

button.active {
  color: #101112;
  background: white;
  border: 1px solid #000ad2;
}

button {
  border: none;
  box-shadow: none;
  outline: none;
  background: #000ad2;
  height: 40px;
  padding: 0px 20px;
  font-weight: bold;
  font=size: 24px;
  color: white;
  transition:  transform 0.5s ease-out;
  cursor: pointer;
}

button:not(.active):hover {
  transform: scale(1.1);
  transition:  transform 0.5s ease-out;
}

h1 {
  text-align: center;
}

.invesco-logo {
  height: auto;
  width: 80px;
  display: inline-block;
  vertical-align: middle;
  padding-bottom: 4px;
}

